import { Component, OnInit } from '@angular/core';
import {Product} from '../../models/product';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  products = [];
  slideConfig = {
    slidesToShow: 6,
    slidesToScroll: 1,
    dots: false,
    autoplay: true,
    prevArrow: `<svg class="prevBtn" viewBox='0 0 512 512'>
        <use href="../../../assets/icons/home/chevron-back-outline.svg#el"
               x="0"
               y="0" />
</svg>`,
    nextArrow: `<svg class="nextBtn" viewBox='0 0 512 512'>
        <use href="../../../assets/icons/home/chevron-forward-outline.svg#el2"
               x="0"
               y="0" />
               </svg>`,
  };

  howConfig = {
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: true,
    autoplay: true,
    arrows: false,
  };
  constructor() { }

  ngOnInit(): void {
    for (let i = 0; i < 20; i++) {
      this.products.push(new Product({
        category: 'Ключи',
        name: 'Bulletstorm: Full Clip Edition ' + i,
        image: '/src/assets/dev/product.png',
        discount: i % 3 === 2 ? 30 : 0,
        price: 2500,
        isNew: i % 3 === 0,
        isHit: i % 3 === 1
      }));
    }
  }

  removeSlider(event, slider): void {
    event.preventDefault();
    event.currentTarget.classList.add('hide');
    slider.unslick();
  }
}
